<script>
export default {
  name: "Navbar",
  props: {
    siteData: {
      type: Array,
      required: true
    },
    place: {
      type: Object,
      required: true
    },
    weatherData: {
      type: Object,
      required: true
    }
  },
  computed: {
    getPlace: {
      get() {
        return this.place.siteNum;
      },
      set(val) {
        this.$emit("update:place", {
          siteNum: Number(val),
          siteName: String(this.siteData[val - 1].name),
          areaNum: Number(this.siteData[val - 1].towns[0].id),
          areaName: String(this.siteData[val - 1].towns[0].name)
        });
      }
    },
    nav_dataWeather: {
      get() {
        return this.weatherData;
      },
      set(val) {
        this.$emit("update:weatherData", val);
      }
    }
  },
  methods:{
    navWeather(){
      let d = new Date().getTime();
      let weather_url = `https://works.ioa.tw/weather/api/weathers/${
        this.place.areaNum
      }.json?_=${d}`;
      this.axios({
        method: "get",
        url: weather_url
      }).then(res => {
        (this.Loading = true), (this.nav_dataWeather = res.data);
      });
    }
  }
};
</script>

<template src="./template.html"></template>
<style lang="scss" scoped src="./style.scss"></style>
