<template>
  <div id="app">
    <Navbar :siteData="siteData" :place.sync="place" :weatherData.sync="weatherData" />
    <section v-if="Loading === false">
      <Loading/>
    </section>
    <router-view :siteData="siteData" :showData="showData" :place.sync="place" :weatherData.sync="weatherData" v-else/>
  </div>
</template>

<script>
import Navbar from "@/components/Navbar/index.vue";
import Loading from "@/components/Loading/index.vue";

export default {
  name: "app",
  data() {
    return {
      Loading: false,
      showData: [],
      siteData: [],
      weatherData: {},
      place: {
        siteNum: 0,
        areaNum: 0,
        siteName:"",
        areaName:""
      }
    };
  },
  components: {
    Navbar,
    Loading
  },
  mounted() {
    // 展覽資訊
    let show_url =
      "https://cloud.culture.tw/frontsite/trans/SearchShowAction.do?method=doFindTypeJ&category=6";
    this.axios({
      method: "get",
      url: show_url
    }).then(res => {
      (this.Loading = true), (this.showData = res.data);
    });
    // 地點資訊
    let d = new Date().getTime();
    let address_url = `https://works.ioa.tw/weather/api/all.json?_=${d}`;
    this.axios({
      method: "get",
      url: address_url
    }).then(res => {
      this.Loading = true;
      this.siteData = res.data;
      this.place.siteNum = Number(res.data[0].id);
      this.place.siteName = String(res.data[0].name);
      this.place.areaNum = Number(res.data[this.place.siteNum - 1].towns[0].id);
      this.place.areaName = String(res.data[this.place.siteNum - 1].towns[0].name);
    });
    // 天氣資訊
  }
};
</script>

<style lang="scss">
body {
  margin: 0;
}
</style>
